@font-face {
  font-family: "Fattana";
  src: url("./font/Fattana-Regular.otf");
}

* {
  box-sizing: border-box;
}
html,
body {
  color: #333;
  /* overflow: hidden; */
  background-image: url(../src/img/bg.svg);
}

.con {
  width: 80%;
  height: auto;
  margin: 50px;
  display: grid;
  align-items: center;
  justify-content: center;
  background-color: #000f55;
}

.fut {
  color: #ddd;
  padding: 20px;
  width: 330px;
  height: 40px;
  display: flex;
  margin: 60px auto;
  align-self: center;
  align-content: center;
}

.PrivateSwitchBase-input-196 {
  top: 0;
  left: 0;
  width: 0;
  cursor: none;
  height: 100%;
  position: absolute;
}

.page {
  padding: 10px;
  position: relative;
  background: linear-gradient(to right, #fff, #fff);
  padding: 0px;
  border: 1px solid #ccc;
  top: 0px;
  transform: rotateZ(0deg);
  width: 500px;
  display: inline-block;
  height: 600px;
  box-sizing: border-box;
}
.paper {
  background-image: linear-gradient(#999 0.05em, transparent 0.1em);
  background-color: unset !important;
  line-height: 1.5em;
  background-size: 100% 1.5em;
  padding: unset;
}

.textarea {
  background-color: #fff;
  height: 100%;
  top: 0;
  left: 0;
  outline: none;
  font-family: "Homemade Apple", cursive;
  color: #000f55;
  padding-top: 0px;
  line-height: 1.5em;
  box-sizing: border-box;
  word-spacing: 3px;
  position: relative;
}
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: none;
  background-image: linear-gradient(10deg, #0008, #0000);
}
.page:not(.margined-page) {
  padding: 15px;
}
.paper-content {
  width: 100%;
}
.page:not(.margined-page) .paper-content {
  padding: 0px;
}
/* Page with margin */
.margined-page {
  width: 500px;
  height: 600px;
  overflow: hidden;
}

.paper-margin-left-line {
  display: none;
}
.paper-margin-top-line {
  display: none;
}

.margined-page .paper-margin-left-line {
  display: inline-block;
  width: 80px;
  border-right: 2px solid pink;
  height: 200%;
  top: -100%;
  position: relative;
}

.margined-page .paper-margin-top-line {
  display: block;
  color: #000f55;
  font-family: "Homemade Apple", cursive;
  width: 100%;
  border-bottom: 2px solid pink;
  height: 80px;
}
.date-container {
  padding: 10px;
  border: 1px solid #d68f9b;
  border-radius: 10px;
  display: inline-block;
  font-size: 10pt;
  margin-top: 20px;
  margin-right: 20px;
}

.margined-page .paper-content {
  position: relative;
  top: 0px;
  left: 0px;
  flex: 1;
  padding-left: 10px;
  display: inline-block;
  top: 0px;
}

.margined-page .textarea {
  height: calc(100% - 80px);
}

.row {
  display: flex;
}
.col {
  flex: 1;
}
.form-container {
  padding: 60px 100px;
  font-family: Arial, Helvetica, sans-serif;
  overflow-y: hidden;
}
.form-container label {
  font-weight: bold;
}
.form-container input,
.form-container select {
  display: block;
  width: 100%;
  background-color: #fff;
  border: none;
  border: 1px solid #ddd;
  padding: 10px;
}

.form-container input[type="number"] {
  display: inline-block;
  width: unset;
}
.form-container button {
  border: none;
  background-color: #09f;
  padding: 10px;
  color: #fff;
  font-weight: bold;
  cursor: pointer;
}

/* Switch */
.switch-toggle {
  position: relative;
  display: block;
  margin-top: 6px;
  cursor: pointer;
  width: 40px;
  height: 20px;
  border-radius: 25px;
  background-color: #eee;
}

.switch-toggle input {
  display: none;
}

.switch-toggle div {
  position: absolute;
  border-radius: 50%;
  background-color: #dfdfdf;
  transition: 0.1s ease;
}

.switch-toggle input:checked + div {
  left: 50%;
  background-color: #09f;
}

.switch-toggle.rect {
  border-radius: 0;
}

.switch-toggle.rect div {
  border-radius: 0;
}

.switch-toggle.inner div {
  width: 18px;
  height: 18px;
  top: 1px;
  left: 1px;
}

.switch-toggle.outer div {
  width: 23px;
  height: 23px;
  top: -1.5px;
  left: -1.5px;
}
.blue-button {
  text-decoration: none;
  background-color: #09f;
  color: #fff;
  padding: 10px 15px;
  font-weight: bold;
  font-size: 10pt;
  cursor: pointer;
}
.download-button.disabled {
  opacity: 0 !important;
}
.disabled {
  cursor: not-allowed !important;
  opacity: 0.5;
}
.hide-pc {
  display: none;
}
.hide-mobile {
  display: block;
}

.output-container {
  position: relative;
}

.github-corner:hover .octo-arm {
  animation: octocat-wave 560ms ease-in-out;
}
@keyframes octocat-wave {
  0%,
  100% {
    transform: rotate(0);
  }
  20%,
  60% {
    transform: rotate(-25deg);
  }
  40%,
  80% {
    transform: rotate(10deg);
  }
}
@media (max-width: 500px) {
  .github-corner:hover .octo-arm {
    animation: none;
  }
  .github-corner .octo-arm {
    animation: octocat-wave 560ms ease-in-out;
  }
}

#output1 {
  display: none;
}
.hide-icon {
  display: none !important;
}

@media (max-width: 768px) {
  .row.responsive-row {
    display: block;
  }
  .hide-icon {
    display: block !important;
  }
  .output {
    margin-top: 20px;
    display: block !important;
  }
  .fixx {
    position: fixed;
    background: rgb(189, 0, 189);
    color: rgb(255, 255, 255) !important;
    bottom: 150px;
    padding: 10px;
    border-radius: 1rem 0rem;
  }
  .MuiButton-containedSecondary {
    display: block !important;
  }

  .MuiGrid-spacing-xs-2 > .MuiGrid-item {
    padding: 0px !important;
  }

  .fl {
    margin: 0px;
  }
  .m-input {
    display: none;
  }
  .page {
    width: 460px !important;
  }

  .MuiGrid-grid-xs-5 {
    max-width: 100% !important;
  }

  .MuiContainer-root {
    width: 90% !important;
    padding-right: 5px !important;
    padding-left: 0px !important;
  }
  .output-grid .col:first-child {
    overflow-x: scroll;
  }

  .output img {
    width: 100%;
  }
  .hide-pc {
    display: initial;
  }
  .form-container {
    padding: 40px 10px;
  }
  .hide-mobile {
    display: none;
  }
}

/* DRAW  */
.draw-container {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #000d;
  z-index: 10;
  color: #ddd;
  padding: 100px;
  display: none;
}

.draw-container.show {
  display: block;
}

.draw-container .close-button {
  position: fixed;
  top: 0px;
  right: 0px;
  padding: 20px 30px;
  cursor: pointer;
  color: #ddd;
  background-color: transparent;
  border: none;
  font-size: 50pt;
  font-weight: bold;
}
.draw-container .close-button:hover {
  color: #fff;
  transition: color 0.5s ease;
}

.draw-container .buttons-container > .blue-button {
  display: block;
  margin-bottom: 6px;
  width: 150px;
}
.buttons-container {
  padding: 0px 20px;
}

@media (max-width: 768px) {
  .draw-container {
    padding: 100px 10px;
  }
  .buttons-container {
    padding: 0px 0px;
  }

  .draw-container .close-button {
    font-size: 30pt;
  }

  .draw-container .buttons-container > .blue-button {
    display: inline-block;
  }
}
